.creator {
  width: 100%;
  height: 100%;
  text-align: center;

  .pizza_builder {
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas: "picker1" "preview" "preview" "picker2";
    height: 1000px;

    .ingredients_picker_1 {
      background-color: purple; }

    .ingredients_picker_2 {
      background-color: purple; }

    .pizza_preview {
      grid-area: preview;
      background-color: pink; } } }





@media only screen and (min-width: 993px) {

  .creator {

    .pizza_builder {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-areas: "picker1 preview preview picker2";
      grid-template-rows: auto;
      height: 200px;

      .ingredients_picker_1 {
        grid-area: picker1;
        background-color: orange; }

      .ingredients_picker_2 {
        grid-area: picker2;
        background-color: orange; }

      .pizza_preview {
        grid-area: preview;
        background-color: yellow; } } } }
